.profile__title{
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 114%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

@media screen and (max-width: 425px) {
    .profile__title{
        font-size: 27px;
        line-height: 33px;
    }
}